<template>
  <div id="blackboardWriting">
    <head-top title="板书"></head-top>
    <van-pull-refresh v-model="isLoading" @refresh="getDataList" class="pull_fresh">
      <r-image-text ref="imageText" v-if="dataList.length > 0" :data-list="dataList">
      </r-image-text>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>
  </div>
</template>

<script>
import RImageText from "@/components/r-image/r-image-text";
import { ModulePicture } from "@/utils/enum";
export default {
  name: "",
  components: { RImageText },
  props: {},
  data() {
    return {
      isLoading: false,
      dataList: []
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.isLoading = true;
      this.dataList = [];
      this.$gatewayHttp.boardNote
        .getList({
          CIID: this.$store.getters.courseID
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.sort((a, b) => {
              if (a.CreateTime > b.CreateTime) {
                return -1;
              } else {
                return 1;
              }
            });
            res.Datas.forEach(item => {
              let sub = {
                img: this.$gatewayHttp.resourse.picture(
                  item.BNContent,
                  ModulePicture.BoardNote
                ),
                state: item.State,
                title: item.BNTitle,
                time: item.CreateTime,
                id: item.BNID,
                content: item.BNContent
              };

              let index = this.dataList.findIndex(
                k => JSON.stringify(sub) === JSON.stringify(k)
              );
              // 避免重复数据
              if (index === -1) this.dataList.push(sub);
            });

            // this.autoShow();
            this.$refs["imageText"].autoShow();
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    }
  },
  watch: {
    "$store.getters.boardNote"(newVal, oldVal) {
      if (newVal && newVal.id) {
        this.getDataList();
      }
    }
  }
};
</script>


